import { useEffect, useState } from "react";
import Card from "../UI/Card/Card";
import styles from "./Home.module.css";
import useHttp from "../../hooks/usehttp";
import React from 'react';

const BASE_URL = "https://react-http-b8415-default-rtdb.firebaseio.com/";

function Home() {
  const [user, setUser] = useState({
    first_name: "",
    last_name: "",
    email: "",
  });

  const { isLoading, error, request } = useHttp();

  useEffect(() => {
    const fetchUser = async () => {
      const userId = localStorage.getItem("userId");
      const url = `${BASE_URL}/user.json?orderBy="$key"&equalTo="${userId}"`;

      const data = await request({ url });

      setUser({
        first_Name: data[userId].first_Name,
        last_Name: data[userId].last_Name,
        email: data[userId].email,
      });
    };

    fetchUser();
  }, [request]);

  const loadingMessage = <h2>Cargando...</h2>;

  const errorMessage = <h2>{error}</h2>;

  return (
    <Card className={styles.home}>
      {isLoading && loadingMessage}
      {error && errorMessage}
      {!isLoading && !error && (
        <React.Fragment>
          <h1>Bienvenido!</h1>
          <h2>
            {user.first_name} {user.last_name}
          </h2>
        </React.Fragment>
      )}
    </Card>
  );
}

export default Home;